<template>
  <q-layout class="default-layout">
    <header class="app-header empty">
      <router-link to="/" class="logo-box">
        <img class="mobile-logo" :src="darkMode ? darkLogo : darkLogo" alt="Beenow" width="120" height="60"
          title="Beenow - агрегатор новин">
      </router-link>
      <router-link :to="backUrl" class="back-button">
        <div style="transform: rotate(45deg) translate(1px, -1px);">+</div>
      </router-link>
    </header>
    <q-page-container class="main-box empty">
      <router-view />
    </q-page-container>
    <div v-if="showCookiePanel" class="cookiePanel">
      <p>
        {{ $t("cookie_message") }}
        <br />
        <br />
        {{ $t("thanks") }}!
      </p>
      <button class="close-btn" title="Закрити" @click="closeCookiesPanel">
        +
      </button>
    </div>
  </q-layout>
</template>

<script>
import { ref, defineComponent } from 'vue'
import { StorageService, DarkModeService } from '@/services'

export default defineComponent({
  name: 'MainLayout',
  setup() {
    const showCookiePanel = ref(true)

    const storageService = new StorageService()

    const isHideCookiePanel = storageService.get('isHideCookiePanel')
    const { darkMode } = DarkModeService

    if (isHideCookiePanel) {
      showCookiePanel.value = false
    }

    const closeCookiesPanel = () => {
      showCookiePanel.value = false
      storageService.set('isHideCookiePanel', true, 1.21e6 * 1000)
    }

    const refreshPage = () => {
      location.reload()
    }

    return {
      showCookiePanel,
      darkMode,
      backUrl: history.state.back ? history.state.back : '/',
      darkLogo: require('@/assets/img/dark-logo.svg'),
      whiteLogo: require('@/assets/img/white-logo.svg'),
      host: process.env.VUE_APP_HOST,
      closeCookiesPanel,
      refreshPage,
    }
  },
})
</script>

